<template>
  <div class="info-container">
    <div class="info-div" >
      <div class="info-header info-header-operate" style="right:0;left:0;top:0">
        <div class="operate-div" style="padding:0.2em 0">
            <combine :option="combineOption" @getSelects="getUserInfo" style="margin-right:0"></combine>
        </div>
      </div>
      <div class="info-container detail-container returnCon"  style="margin-top: 2.8em;height:calc(100% - 3em)">
        <div class="list-content special-content list-contentB"  >
          <div class="content-left" v-if="showFlag">
             <p class="HF-p">
               <span></span>
               <i class="hasIcon hasImg"  @click="currentDevice['customerNo'] != '' && refreshRemainAmount(true)" style="margin-right:0" >
                 <img src="dist/img/costControl/fresh.png" style="height:auto" />
               </i>
             </p>
             <p class="content-p" style="margin-bottom:1em">
               <span>{{cusInfo.remainAmount == 'undefined'  || cusInfo.remainAmount.toString() == '' ? '--' : cusInfo.remainAmount.toString()}}</span>
             </p>
             <p>{{cusInfo.amountCollectTime}}</p>
          </div>
          <div class="content-right" :class="{'content-rightMax': !showFlag}">
            <ul class="form-list" :style="{'border-bottom': showFlag ? '1px solid rgba(255,255,255,0.15)' : 'none'}">
              <li v-for="(item, $index) in customerInfo" >
                <span class="label-span">{{item.name}}</span>
                <span class="label-show " style="text-align: left;margin-left:0" v-html="' &nbsp;' +  getValue(item.value)"></span>
              </li>
            </ul>
            <ul class="form-list leftFromUl" v-if="showFlag">
              <li v-for="(obj, $index) in formObj" :class="obj.classes" style="margin-top:0.8em!important;margin-bottom:0!important">
                 <span class="label-span"  style="margin-right: 0.8em;">{{obj.label}}</span>
                 <inputs v-if="obj.type == 'input'" :option="obj.option" @getInput="getForm"></inputs>
                 <selects  v-if="obj.type == 'select'" :option="obj.option"  @getSelect="getForm" ></selects>
              </li>
             </ul>
             <div class="operate-div" style="float:left" v-if="showFlag && controls[31]" >
                  <button  class="button-operate buttonStyle"   @click="confirmReturn" :disabled=" !(cusInfo.remainAmount > 0)">
                    
                  </button>
             </div>
          </div>
        </div>
        <div class="list-content detail-content maxContent list-contentB" :class="{minContent: showFlag}">
          <div class="list-header" style="border:0;float:left;padding:1em  0">
            <i class="hasIcon hasImg" style="margin-top:-0.1em">
              <img src="dist/img/costControl/IC-card-icon.png" />
            </i>
            
          </div>
          <div class="operate-div" style="padding:0.2em 0 0.8em 0">
               <datePicker ref="timer"  type= "doubleDate"  :dateOpt="searchDateOpt" v-if="showFlag"  @getDatePicker="getForm"  @getInitDatePicker="getForm"></datePicker>
          </div>
           <div ref="myOwnTable" class="maxTable">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" ></tables>
           </div>
        </div>
       </div>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="confirm" ref="confirms"></dialogs>
      <confirmUser ref="showUserConfirm" @nextOperate="submitReturn"  tipMsg=""></confirmUser>
    </div>
  </div>
</template>

<script>
    import combine from  '../../../component/childComponent/CombineSelect.vue'
    import confirmUser from  '../../../component/childComponent/userValidate.vue'
    import 'vue-awesome/icons/ticket'
    import 'vue-awesome/icons/podcast'
    import {GUID} from '../../../assets/GUID.js'
    export default {
        components: {
          combine,
          confirmUser
        },
        data() {
            return {
   
                controls: [],
                //
                combineOption: {
                    position: 'right',
                    inputOpt: {
                        width: '100%',
                        type: 'typeSearch',
                        selectOpt: {
                            width: '7em',
                            cs: 'client',
                            data: [
                                {text: '', value: 'customerContact'},
                                {text: '', value: 'customerAddr'},
                                {text: '', value: 'customerName'},
                                {text: '', value: 'propertyName'}
                                //{text: '', value: 'deviceNo'}
                            ]
                        }
                    },
                    url: 'pay/fuzzyQuery',
                    method: 'post',
                },
                searchDate: '',
                //
                returnInfo: {
                    refundMoney: '',      //
                    customerNo: '',   //
                 //   cno: '',          //
                    refundMethod: '', //
                    remark: '', //
                    refundGuid:''
                },
                //
                cusInfo: {
                  localControl: 2,
                  isEnabled: 1,
                  remainAmount: ''
                },
                searchParam:{
                  startDate: '',
                  endDate: '',
                  customerNo: '',   //
                 // cno: ''          //
                },
                currentDevice: {
                  customerNo: '',
                  deviceNo: ''
                },
                TableOpt: {
                    column: [
                        {title: '', field: 'isValid', textAlign: 'center', width: '80',   type:"string", formatter: function (val) {
                            if(val === 0){
                                return '<span class="red"></span>';}
                            else{
                                return '<span></span>';}
                        }},
                        {title: '', field: 'refundMethod',textAlign: 'center',width: '120', type:"string", formatter: function (val) {
                              if(val == 1) {
                                 return '';
                              }
                              return '--'
                        }},
                        {title: '', field: 'refundMoney', textAlign: 'center', width: '100'},
                        {title: '', field: 'afterRemainAmount', textAlign: 'center', width: '110'},
                        {title: '', field: 'remarks', textAlign: 'center', width: '140'},
                        {title: '', field: 'createUserName', textAlign: 'center', width: '100'},
                        {title: '', field: 'refundDate', textAlign: 'center', width: '130'},
                        {title: '', field: 'serialNum', textAlign: 'center', width: '140'},
                    ],
                    queryParam: {},
                   // url: 'refund/queryListByCst',
                  //  method: 'post',
                    data: [],
                    pageSize: 9999,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    isExport: true,
                    noExportCurrent: true,
                    exportURL: 'refund/listByCstDown'
            }
          }
        },
        methods: {
         
          //
          confirmReturn: function () {
             if(this.returnInfo.refundMoney.toString() == '' || this.returnInfo.remark == '') {
                this.alerts.show({msg: ''})
             } else if(this.returnInfo.refundMoney == 0 || this.returnInfo.refundMoney > this.cusInfo.remainAmount) {
                this.alerts.show({msg: '0'})
             } else {
                let that = this;
                that.$refs.showUserConfirm.show();
               
             }
          },
          submitReturn: function () {
            let that = this;
            //that.returnInfo.cno = that.cusInfo.cno;
            that.returnInfo.customerNo = that.cusInfo.customerNo;
            that.returnInfo.refundGuid = new GUID().newGUID().split('-').join('');
            that.getRequest({
              url: 'refund/customerRefund',
              method: 'post',
              param: that.returnInfo,
              success: function (data) {
                that.returnInfo = that.cleanSearchData(that.returnInfo);
                that.alerts.show({msg: data.message});
                that.refreshRemainAmount();
                that.$refs.myTable.resetTableOpt({queryParam: that.searchParam})
              },
              error: function (data) {
                that.alerts.show({msg: data.message})
              }
            })
          },
          //
          getUserInfo: function (data) {
            if(data !== '') {
               this.currentDevice['customerNo'] = data.value;
               this.queryCustomer({sParam: this.currentDevice});
            }
          },
          queryCustomer: function (param, sign) {
            let that = this;
            let sParam = param.sParam;
            if(sParam.customerNo != that.cusInfo.customerNo){
                that.cusInfo = that.cleanSearchData(that.cusInfo);
                that.searchDate = '';
            }
            this.getRequest({
                url: 'pay/queryUser',
                method: 'post',
                param: sParam,
                success: function (data) {
                   let info = data.data;
                   for(let key in info) {
                     that.$set(that.cusInfo,key,info[key]);
                   }
                  that.$set(that.cusInfo,'isEnabled',1);
                  that.$set(that.cusInfo,'localControl',2);
                  that.searchParam.customerNo = that.currentDevice['customerNo'];
                  if(sign) {
                     that.alerts.show({msg: ""});
                  }
                },
              error: function () {
                that.alerts.show({msg: ""});
              }

            });
          },

          //
          refreshRemainAmount: function (flag) {
            let sign = false;
            if(flag) {
                sign = true
            }
            this.queryCustomer({sParam:this.currentDevice}, sign);
          },
            //
          keyUp: function (e) {
                let strChar = e.key;
                if (!/[0-9]|[.]/.test(strChar)) {
                    let re = new RegExp(strChar, "gi");
                    this.returnInfo.refundMoney = this.returnInfo.refundMoney.replace(re, '');
                }
                if (this.returnInfo.refundMoney.length > 9) {
                    this.returnInfo.refundMoney = this.returnInfo.refundMoney.substring(0, 9);
                }
            },
            //
          inputFocus: function () {
                let inputVal = parseFloat(this.payInfo.payMoney);
                this.payInfo.payMoney = inputVal.toFixed(2);
          },
          getForm: function (val) {
            this.getOption(val, this)
          },
          resetTable: function () {
            let that = this;
            if(that.$refs.myTable) {
                if(that.currentDevice['customerNo'] != '') {
                    that.$refs.myTable.resetTableOpt({queryParam: that.searchParam, method: 'post', url: 'refund/queryListByCst'})
                } else {
                    that.$refs.myTable.resetTableOpt({queryParam: that.searchParam})
                }
            } 
          },
          init: function () {
             this.TableOpt.isExport = this.controls[6]
          },
          windowChange: function () {
             let that = this;
            that.getHeight();
          }
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();
            /*window.onresize = function () {
                that.getHeight();
            }*/
        },
        computed: {
            searchDateOpt: function () {
    
              let dates = this.initDate(true);
              return {
                    key: 'searchDate',
                    width: '23em',
                    value: {startDate: dates.start, endDate: dates.end},
                    fontSize: '0.8em'
                }
            },
            showFlag: function () {
               return this.cusInfo.isEnabled == 1 && this.cusInfo.localControl != 1 
            },
            alerts: function () {
                return this.$refs.alerts
            },

            //
            customerInfo: function () {
               return  [{name: '', isShow: true, value: this.cusInfo.customerName},
               {name: '', isShow: true, value: this.cusInfo.propertyName},
               {name: '', isShow: true, value: this.cusInfo.customerContact},
               {name: '', isShow: true, value: this.cusInfo.customerAddr},
              // {name: '', isShow: true, value: this.cusInfo.powerType},
               {name: '', isShow: true, value: typeof this.cusInfo.overdraftAmount == 'undefined' ? '--' : this.cusInfo.overdraftAmount.toString()},
               {name: '1', isShow: true, value: this.cusInfo.alarmThreshold || '0'},
               {name: '2', isShow: true, value: this.cusInfo.alarmThreshold1 || '0'},
               {name: '3', isShow: true, value: this.cusInfo.alarmThreshold2 || '0'},
              // {name: '', isShow: true, value: this.cusInfo.electMeterNo},
              // {name: '', isShow: true, value: this.cusInfo.meterUserNo},
              // {name: '', isShow: true, value: this.getEnumItem('localControl', this.cusInfo.localControl).text}
             ]
           },
           formObj: function () {
             let width = 'calc(100% - 8.5em)';
             return [
                {
                   label: '',
                   type: 'input',
                   classes: 'halfWidth ',
                   option: {
                     type: this.cusInfo.remainAmount > 0 ? 'normalInput' : 'disabledInput',
                     width:width,
                     normal: true,
                     key: 'returnInfo.refundMoney',
                     placeholder: '',
                     value: this.returnInfo.refundMoney,
                     limit: 'floatHasTwo',
                     dataType: '+float',
                    // regular:'float',
                     length: 9
                  }
                },
                {
                   label: '',
                   classes: 'halfWidth hasMarginLi',
                   type: 'select',
                   option: {
                     cs: 'client',
                     width:width,
                     key: 'returnInfo.refundMethod',
                     data: [{text: '', value: '1'}]
                  }
                },
                {
                   label: '',
                   type: 'input',
                   classes: 'allWidth',
                   option: {
                     type: this.cusInfo.remainAmount > 0 ? 'normalInput' : 'disabledInput',
                    // type: 'textArea',
                     normal: true,
                     value: this.returnInfo.remark,
                     width:width,
                     key: 'returnInfo.remark',
                     placeholder: '200',
                     length:  200
                  }
                }
             ]
           }
        },
        watch: {
          searchDate: {
            handler(val, oldVal) {
             
              if(val !== oldVal && val !== '') {
                let arr = val.split('');
                this.searchParam.startDate = arr[0];
                this.searchParam.endDate = arr[1];
                let that = this;
                that.$nextTick(function () {
          
                   that.searchParam.customerNo = that.currentDevice['customerNo'];
                   that.resetTable();
                   
                })
              }
            }
          }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/main.scss";
    $leftContentH: 1em;
    $leftContentW: 16%;
    $leftContentMinW: 23.5em;
    .detail-container{
      margin-top: 3.3em;
      height: calc(100% - 3em);
      .special-content {
        padding:0!important;
        .content-left{
          width: $leftContentW;
          font-size: 0.875em;
          margin: 1.5em;
          padding: 1em 1.25em 1em;
          min-height: $leftContentH;
          border: 0.2em solid #3E8BDB;
          border-radius: 0.4em;
          float:left;
          p.HF-p{
            font-size: 1em;
            text-align:left;
            margin:0;
            i.hasIcon{
              float:right;
              color: #3E8BDB;
              margin-right: 0.5em;
              cursor: pointer;
            }
            i.hasIcon:hover{
              color: white
            }
          }
          p.content-p{
            margin: 2em;
            text-align: center;
            span{
              font-size: 2.7em;
              color: #EE8836;
              margin-right:0.3em;
              font-weight: 600;
            }
          }
        }
        .content-right{
          float:left;
          margin: 1em 0;
          width: calc(100% - #{$leftContentW} - 6.5em);
          min-height: calc(#{$leftContentH} + 0.4em + 2.5em);
          .form-list{
            li {
              .label-show{
                min-width:7em
              }
              .operate-div{
                padding: 0;
                margin-left: 2.5em
              }
            }
          }
          .leftFromUl{
              max-width: calc(100% - 20em);
              float:left;
           }
        
        }
        .content-rightMax{
          margin: 1em ;
          width: calc(100% - 2em)
        }
      }
      .list-content:after,  .form-list:after{
        @extend .clearElement
      }
    }
    
    .buttonStyle {
        //float: left;
        margin: 1em 0;
        padding:0.3em 2em
    }
    .buttonStyle:disabled{
        background: #4d5f77
    }
  
  .returnCon{
      .maxTable{
        float:left;
        height: calc(100% - 3.5em);
        font-size:0.94em
      }
     .maxContent{
        padding: 0.8em 1.2em!important;
        height: calc(100% - 10.5em);
     }
     .minContent{
        height: calc(100% - 15.5em);
     }

  }

 
</style>



